#season-selector {
  margin: auto;
  margin-top: 40px;
  width: 80%;
  height: 400px;
  background-color: rgba(241, 241, 241, 0.63);
  border-radius: 10px;
}

#season-selector > #season-title {
  font-family: 'Stardew';
  font-size: 3em;
  padding-top: 15px;
}

#season-selector .season-selection-card {
  width: 160px;
  height: 200px;
  display: inline-block;
  background-color: rgba(224, 224, 224, 0.541);
  margin: 0 15px 0 15px;
  border-radius: 15px;
  cursor: pointer;
}

#season-selector .season-selection-card:hover {
  background-color: rgb(184, 255, 208);
}

#season-selector .season-selection-card > img {
  height: 64px;
  width: auto;
  margin: 25px auto;
  border-radius: 2px;
}

#season-selector .season-selection-title {
  font-family: 'Stardew';
  font-size: 2em;
  margin-top: 5px;
}

#season-selector.fade-me-in {
  -webkit-animation: fadein 0.5s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
